import React, { Component } from 'react';
import { Formik } from 'formik';
import { connect } from 'react-redux';
import {login, logout} from "../../../actions";
import { Icon, Input, Button } from 'antd';
import { withRouter, Redirect } from "react-router";
import agent from "../../../agent";

class Login extends Component {
	onLogin = () => {
		agent.login('aaa', 'bbb').then((response) => {
			if (response.result == "success") {
				this.props.login(response.token);
				this.props.history.push("/admin");
			} else {

			}
		});
	};

	onLogout = () => {
		this.props.logout();
	};

	render() {
		if (this.props.authorized) {
			return (
				<Redirect to="/admin" />
			);
		}

		return (
			<Formik onSubmit={ (values) => console.log('Values:' + values)}>
				{({
						values,
						errors,
						touched,
						handleChange,
						handleBlur,
						handleSubmit,
						isSubmitting,
						/* and other goodies */
				}) => {

					const myHandleSignIn = () => {
						console.log('myHandleSignIn');

						handleSubmit(arguments);
					};

					return (<div className="loginPage">
						<div className="loginContainer">
							<div className="loginTitle">Sign In</div>
							<div className="loginControlGroup">
								<Input
									name="username"
									onChange={handleChange}
									onBlur={handleBlur}
									prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)'}}/>}
									placeholder="Username"
								/>
								<div className="loginValidationError">Invalid username</div>
							</div>
							<div className="loginControlGroup">
								<Input.Password
									name="password"
									onChange={handleChange}
									onBlur={handleBlur}
									prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>}
									type="password"
									placeholder="Password"
								/>
								<div className="loginValidationError">Invalid username</div>
							</div>
							<div className="loginActions">
								<Button type="primary" className="loginSignInButton" onClick={myHandleSignIn}>Sign In</Button>
							</div>
							<div className="loginActions"
									 style={{'display': 'flex', 'flexDirection': 'row', 'background': 'rgba(0,0,0,0.1)'}}>
								<Button type="primary" className="loginSignInButton" style={{margin: '5px'}}
												onClick={this.onLogin}>Login</Button>
								<Button type="primary" className="loginSignInButton" style={{margin: '5px'}}
												onClick={this.onLogout}>Logout</Button>
							</div>
							<div className="loginActions">
								<div className="loginHint">Hint: demo / demo</div>
							</div>
						</div>
					</div>);
				}}
			</Formik>
		);
	}
}

const mapDispatchToProps = {
	login,
	logout
};

function mapStateToProps(state) {
	return {
		authorized: state.authorized,
	};
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Login));
